.jobs .titre{
    background-image: url('../../images/recrutment.webp');
    width: 100%;
    height: 200px;
    background-size: cover;
    background-position: 70% 70%;
    padding: 0;

}
  
.company_description{
    text-align: justify;
}

.joblist{
    padding: 0;
}
.joblist .item{
    list-style-type: none;

}
.jobcard{
    background-color: #005BFF;
    aspect-ratio: 1/1;
    padding: 30px;
    margin-bottom: 30px;
    position: relative;
    width: 90vw;
    max-width: 450px;
    border: none;
    text-align: left;
}
.jobcard .logo{
    display: flex;
    margin-bottom: 40px;

}
.jobcard p{
    color: white ;

}
.jobcard .date{
    font-size: 0.875rem;
}
.cardinfo{
    flex-direction: column;
    height: auto;
}
.jobs h3{
    margin-bottom: 40px;
    color: white ;

}
.cardinfo div{
    display: flex;
    height: 20px;
    align-items: center;
    margin-bottom: 20px;
}
.cardinfo p{
    margin-left: 20px;
}
.jobicon{

    width: 25px;
}
.jobcard::after{
    content: "\2192";
    position: absolute;
    top: 30px;
    right: 40px;
    font-size: 2rem;
    color: white;
}

.jobs .titre2{
    background-image: url('../../images/jobsbanner.jpg');
    position: absolute;
    left: 0;
    top:100px;
    width: 100%;
    height: 30vw;
    background-size: cover;
    background-position: 50% 70%;
    background-color: red;
    padding: 0;
}
.detailContainer{
    margin-top: calc(80px + 30vw);
}
.jobinfo{
    width: 100%;
    background-color: #E9F1F9;
    font-size: 0.875rem;
    padding-top: 1px;
    padding-bottom: 1px;

}
.jobinfo p{
    margin: 30px;
    padding: 0;
}
.jobinfo span{
    font-weight: 900;
}