.energy .titre{
    background-image: url('../../images/dpe.jpg');
    width: 100%;
    height: 200px;
    background-size: cover;
    background-position: 70% 70%;
    padding: 0;
    margin-bottom: 30px;

}
.energy p{
    text-align: center;
    line-height: 30px;
    padding: 30px;
    margin: 0;
    max-width: 400px;

}
.energybtn{
    width: calc(100% - 60px);
    border: none;
    background-color: #FE5716;
    color: white;
    font-size: 1.125rem;
    border-radius: 8px;
    padding: 10px;
    font-weight: bold;
    font-size: 19px;
}
.energy{
    display: flex;
    align-items: center;
    flex-direction: column;
}
.number{
    color: #FE5716;
    font-size: 1.5rem;
    font-weight: 700;
}
.form{
    background-color: #005BFF;
    width: 100%;
}
.form  *{
    color: white ;
}
.form h2{
    color: white;
}
.form h2::after{
    background-color: white;
}
.input_container{
    background-color: white;
}


/* input css*/

.input_group{
    display: flex;
    flex-direction: column;
    margin-top: 0;
    align-items: center;
}
label{
    color: black !important;
    font-weight: 900;
    margin-bottom: 15px;
}
label:focus-within{
    color: #005BFF !important;
}
input{
    color: #001a70 !important;
    padding-left: 30px;
    height: 50px;
    width: calc(100vw - 90px);
    outline: none;
    border: 2px solid;
    border-radius: 5px;
    margin: 0;
}
select{
    color: #001a70 !important;
    padding-left: 30px;
    height: 50px;
    width: calc(100vw - 55px);
    outline: none;
    border: 2px solid;
    border-radius: 5px;
    margin: 0;
    
}
input:focus{
    border-color: #005BFF !important;
}
.input_container{
    padding-top: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-left: 27px;
    padding-right: 27px;

}
.carousel {
    display: flex;
    overflow: hidden;
    scroll-behavior: smooth;

}
option{
    color: #001a70 !important;
    outline: none;
    appearance: none;
}
select{
    outline: none;
    appearance: none;

}
#selector option:hover{
    background-color: red;
}