@import url('https://fonts.googleapis.com/css2?family=Irish+Grover&family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap');
.App {
  text-align: center;
}
*{
  font-family: 'Open Sans', sans-serif ;
  color: #001A70;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container{
  width: 100%;
  padding-top: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  
}
h1{
  color: white;
  margin: 0;
  display: table-cell;
  vertical-align:  bottom;
  font-weight: 00;
  padding-bottom: 30px;
  font-size: 2rem;
  
}
.dark{
  background-color: rgba(0, 0, 0, 0.5);
  height: 100%;
  display: table;
  padding-left: 30px;
  width: calc(100% - 30px);
}
h2{
  color: #FE5716;
  font-weight: 400;
  margin: 30px;
  text-align: center;
  position: relative;
  margin-bottom: 60px;

}
h2::after{
  content: "";
  position: absolute;
  height: 5px;
  width: 100px;
  background-color: #FE5716;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%);
}
p{
  padding: 30px;
}