header{
    height: 80px;
    top: 0px;
    width: 100vw;
    font-weight: 600;
    display: grid;
    grid-template-columns: 300px auto;
    align-items: center;
    background-color: white;
    transition: top .3s;
    position: fixed;
    --main-color: #FE5716;
    box-shadow: 0 0 20px #0000001a;
    z-index: 999;

}
.logo{
    display: grid;
    grid-template-columns: 30% auto;
    align-items: center;


}
.icone{
    width: 40px;
    justify-self: center;
    margin-left: 15px;
    margin-right: 15px;
}
.typo{
    height: 70px;
}

.burgerLine{
    width: 32px;
    height: 3px;
    background-color: var(--main-color);
    transition: .3s;
    border: solid;
    border-width: 1px;
    border-radius: 5px;
    border-color: var(--main-color);
    margin-right: 30px;
    align-self: flex-end;
}
.burgerIcon{
    display: flex;
    flex-direction: column;
    height: 30px;
    justify-content: space-around;
}
.modalBackground{
    position: inherit;
    height: calc(100vh - 80px);
    width: 100vw;
    top:80px;
    background-color: var(--main-color);
    z-index: inherit;

}
.modalContainer{
    display: grid;
    height: 100%;
}
.menu_mobile{
    list-style-type: none;
    padding: 0;
    color: white;

}
.menu_mobile li{
    text-align: start;
    padding: 20px;
    font-size: 1.5rem;
    font-weight: 900;
}

.navbar{
    list-style-type: ' ';
    display: none;
    padding: 0;
    margin: 0;
}
.navbar li{
    position: relative;
}
.navbar li:hover{
    color: #F55D1C;

}
.navbar li::before{
    content: '';
    position: absolute;
    height: 10px;
    width: 0%;
    bottom:-15px;
    left: -5px;
    transition: all .3s;

}
.navbar li:hover::before{
    content: '';
    background-color: var(--main-color);
    position: absolute;
    height: 10px;
    width: 100%;
    bottom:-15px;
    left: -5px;
    transition: all .3s;

}
@media  only screen and (min-width:600px){
    header{
        height: 110px;

    }


}

