}

@media  only screen and (min-width:992px){


}

@media  only screen and (min-width:1200px){
    header .container{
        flex: auto;
        display: flex;
        align-items: center;
        justify-content: flex-end;

    
    }
    .navbar{
        display: flex;
        align-items: center;
        justify-content: space-around;
        flex: auto;
    }
    .burgerIcon{
        visibility: hidden;
    }
    .burgerLine{
        margin: 0;
    }

}
@media  only screen and (min-width:1400px){
    .navbar{
        max-width: 65vw;
    }
}
