.customers .titre{
    background-image: url('../../images/happy.webp');
    width: 100%;
    height: 200px;
    background-size: cover;
    background-position: 50% 40%;
    padding: 0;
}

.instal{
    margin-top: 30px;
    padding: 30px;
}
.comment{
    background-color: #DF3603;
    padding: 30px;
    margin-top: 30px;
    border-top-left-radius: 25px;
    border-bottom-right-radius: 25px;

}
.feedback{
    background-color: #FFF0EB;
    padding: 30px;
    position: relative;
    list-style-type: none;
    flex: 0 0 calc(100% - 120px);
    padding-top: 70px;
    margin: 30px;
}
.feedbacklist{
    padding: 0;
    display: flex;
    width: 100vw;
    overflow: hidden;
    scroll-behavior: smooth;
}
.quote{
    background-color: #FE5716;
    position: absolute;
    top: 0;
    left: 0;
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;

}
.quote2{
    background-color: #FE5716;
    position: absolute;
    bottom: 0;
    right: 0;
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.feedback p{
    font-size: 1.5rem !important;
    color: black;
    margin: 0;
}
.feedback strong{
    color: black;
}

#prevBtn, #nextBtn {
    color: #1057C8;
    font-size: 2rem;
    background-color: white;
    border: none;
}
.buttons{
    display: flex;
    justify-content: space-around;
    background-color: white;
    padding-bottom: 30px;
}